import React from 'react';
import styles from "../../pages/privacypolicy/PrivacypolicyLayout.scss";
import classnames from 'classnames';

const cx = classnames.bind(styles);

class _PrivacyPolicyComponent06 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            view: false
        };

        this.onClickHandler = this.onClickHandler.bind(this);
    }

    onClickHandler() {
        this.setState({
            view: !this.state.view
        })
    }

    render() {
        return (
            <li>
                <h2 className={cx(styles['terms-section--terms-contents--title'], this.state.view ? styles['active'] : null)} onClick={this.onClickHandler}>6.    </h2>
                <div className={cx(styles['terms-section--terms-contents--list'], this.state.view ? styles['active'] : null)}>
                    <strong>            .     .</strong>
                    <ul className={cx(styles['__description-ordered'], styles['__middle-height'])}>
                        <li className={styles['__middle-height']}>1. 
                            <ol>
                                <li>1)          ,         (  
                                    )      .           
                                     .
                                </li>
                            </ol>

                        </li>
                        <li className={styles['__middle-height']}>2. 
                            <ol>
                                <li>1)             .</li>
                                <li>2)        .</li>
                            </ol>
                        </li>
                    </ul>
                </div>
            </li>
        )
    }
}

export default _PrivacyPolicyComponent06
;