import React from 'react';
import styles from "../../pages/privacypolicy/PrivacypolicyLayout.scss";
import classnames from 'classnames';

const cx = classnames.bind(styles);

class _PrivacyPolicyComponent01 extends React.Component {


    constructor(props) {
        super(props);
        this.state = {
            view:false
        };

        this.onClickHandler = this.onClickHandler.bind(this);
    }

    onClickHandler(){
        this.setState({
            view:!this.state.view
        })
    }

    render() {
        return (
            <li>
                <h2 className={cx(styles['terms-section--terms-contents--title'], this.state.view ? styles['active']:null)} onClick={this.onClickHandler}>1.     </h2>
                <div className={cx(styles['terms-section--terms-contents--list'], this.state.view ? styles['active']:null)}>
                    <strong>         .</strong>
                    <p className={styles['__middle-height']}>[  ]</p>
                    <p> (iOS/Android ),  (////), , ,    </p>
                    <p className={styles['__middle-height']}>[  ]</p>
                    <p>, ,     </p>
                    <p className={styles['__middle-height']}>[   ]</p>
                    <ul className={styles['__description-unordered']}>
                        <li>  &amp;       / </li>
                        <li>   BLE     OS         Broadcasting    ,   ,        </li>
                        <li> //       </li>
                        <li>        (///)     </li>
                        <li> Wi-Fi      </li>
                        <li>         ( )  </li>
                    </ul>
                </div>
            </li>
        )
    }
}

export default _PrivacyPolicyComponent01;