import React from 'react';
import styles from "../../pages/privacypolicy/PrivacypolicyLayout.scss";
import classnames from 'classnames';
const cx = classnames.bind(styles);
class _PrivacyPolicyComponent04 extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            view:false
        };

        this.onClickHandler = this.onClickHandler.bind(this);
    }

    onClickHandler(){
        this.setState({
            view:!this.state.view
        })
    }
    render() {
        return (
            <li>
                <h2 className={cx(styles['terms-section--terms-contents--title'],this.state.view ? styles['active']:null)} onClick={this.onClickHandler}>4.    </h2>
                <div className={cx(styles['terms-section--terms-contents--list'], this.state.view ? styles['active']:null)}>
                    <ul className={styles['__description-ordered']}>
                        <li className={styles['__middle-height']}>1.             [   ]         , ,   .</li>
                        <li className={styles['__middle-height']}>2.             .
                            <ol>
                                <li>1)     </li>
                                <li>2) ,               </li>
                                <li>3)        </li>
                                <li>4)    </li>
                            </ol>
                        </li>
                        <li className={styles['__middle-height']}>3.           ,              .
                                   .
                        </li>
                    </ul>
                </div>
            </li>
        )
    }
}

export default _PrivacyPolicyComponent04;