import React from 'react';
import classnames from 'classnames';
import styles from "../../pages/termsofuse/TermsOfUseLayout.scss";
const cx = classnames.bind(styles);

class _TermsComponent06 extends React.Component{

    constructor(props){
        super(props);
        this.state ={
            view:false
        }
        this.onClickHandler = this.onClickHandler.bind(this);
    }

    onClickHandler(){
        this.setState({
            view:!this.state.view
        });
    }

    render(){
        return(
            <li>
                <h2 className={cx(styles['terms-section--terms-contents--title'], this.state.view ? styles['active']:null)} onClick={this.onClickHandler}><em>6</em></h2>
                <div className={cx(styles['terms-section--terms-contents--list'], this.state.view ? styles['active']:null)}>
                    <ol className={styles['__description-ordered']}>
                        <li>1.                .</li>
                        <li>2.  1               .
                            <ol>
                                <li>1)    7 3       ,  7 3     3         .</li>
                                <li>2)   , ,   </li>
                                <li>3)           </li>
                            </ol>
                        </li>
                        <li>3.         .</li>
                        <li>4.        ,             .</li>
                    </ol>
                </div>
            </li>
        )
    }
}

export default _TermsComponent06;